<template>
  <section class="maxBox">
    <div class="top">
      <img src="http://lianai-image-sys.qiniudn.com/t/newBg.jpg" alt="" />
      <button class="paybtn" @click="jumpCharge"></button>
      <!--倒计时-->
      <div class="top_time" v-if="remainSecond>0">
        <p><strong class="top_day"><s></s><span class="time">{{day<10?'0':day[0]}}</span><span class="time">{{day<10?day:day[1]}}</span></strong>天</p>
        <p><strong class="top_hour"><s id="h2"></s><span class="time">{{hour<10?'0':hour[0]}}</span><span class="time">{{hour<10?hour:hour[1]}}</span></strong>时</p>
        <p><strong class="top_minute"><s></s><span class="time">{{minute<10?'0':minute[0]}}</span><span class="time">{{minute<10?minute:minute[1]}}</span></strong>分</p>
        <p><strong class="top_second"><s></s><span class="time">{{second<10?'0':second[0]}}</span><span class="time">{{second<10?second:second[1]}}</span></strong>秒</p>
      </div>
      <!--倒计时-->
    </div>
    <!--排行榜-->
    <div class="listBox">
      <!--  -->
      <div class="list_me" v-for="(item,index) in list" v-if="index==0&&!isShare">
        <div class="list_ranking">{{item.ranking>0?item.ranking:'未上榜'}}</div>
        <!--排名-->
        <div class="list_pic"><img :src="item.head" alt="" /></div>
        <!--头像-->
        <div class="list_name">{{item.name}}</div>
        <div class="list_number">福气值&nbsp;|&nbsp;<em>{{item.value}}</em></div>
      </div>
      <!--我的排名-->
      <!--用户排名-->
      <ul class="user_list">
        <li v-for="(item,index) in list" v-if="index>0">
          <div class="user_listBox">
            <div class="list_ranking user_ranking"><i>{{index}}</i></div>
            <!--排名-->
            <div class="list_pic">
              <img :src="item.head" alt="" />
              <i v-if="index==1"><img src="http://lianai-image-sys.qiniudn.com/mq/pic_1.png" alt=""/></i>
              <!--皇冠-->
              <i v-if="index==2"><img src="http://lianai-image-sys.qiniudn.com/mq/pic_2.png" alt=""/></i>
              <!--皇冠-->
              <i v-if="index==3"><img src="http://lianai-image-sys.qiniudn.com/mq/pic_3.png" alt=""/></i>
              <!--皇冠-->
            </div>
            <!--头像-->
            <div class="list_name">{{item.name}}</div>
            <!--昵称-->
            <div class="list_number">福气值&nbsp;|&nbsp;<em>{{item.value}}</em></div>
            <!--福气值-->
          </div>
        </li>
      </ul>
    </div>
    <!--排行榜-->
    <div class="footer"></div>
    <button class="refurbish" @click="reload"></button>
    <!--刷新按钮-->
    <!-- <div class="r_footer_block"></div> -->
    <div class="down" @click="jumpCharge">
      <img src="http://lianai-image-sys.qiniudn.com/mq/cpaybtn.png 

" alt="" />
      <!--下载Btn-->
    </div>
    <!--  <div class="r_footer"><img :src="imgChange?'http://lianai-image-sys.qiniudn.com/wa/bg_foot_1.png':'http://lianai-image-sys.qiniudn.com/wa/bg_foot_2.png'" alt="" @click="jumpDownload"></div> -->
  </section>
</template>
<script>
import user from './assets/js/user.js'
import doll from './api/doll/doll'
import common from './common/common.js'
import httpUtils from './common/httpUtils.js'

export default {
  name: 'app',
  components: {},
  data() {
    return {
      uid: 1832306,
      userInfo: {},
      list: [],
      imgChange: true,
      inviteCode: 0,
      remainSecond: 0,
      day: 0,
      hour: 0,
      minute: 0,
      second: 0,
      isShare:true
    }
  },
  filters: {
    fmtDate: function(value) {
      if (!value) return ''
      return common.fmtDate(value);
    }
  },
  created: function() {
    this.uid = httpUtils.request("uid");
    console.log(this.uid);
    this.getList();
    this.getState();
    setInterval(() => {
      this.changeImg();
    }, 500)
  },
  methods: {
    jumpDownload: function() {
      httpUtils.jumpDownloadUrl();
    },
    getList: function() {
      let self = this;
      doll.getChargeRank(this.uid).then(function(res) {
        console.log(res);
        self.list = res.rankingList;
      }).catch(function(err) {
        console.log(err);
      });
    },
    getState: function() {
      let self = this;
      doll.getStatus(this.uid).then(function(res) {
        console.log(res);
        self.remainSecond = res.residueTime;
      }).catch(function(err) {
        console.log(err);
      });
      setInterval(() => {
        this.handleCounter();
      }, 1000)
    },
    handleCounter: function() {
      if (this.remainSecond > 0) {
        this.remainSecond = this.remainSecond - 1;
        let day = Math.floor(this.remainSecond / (60 * 60 * 24));
        let hour = Math.floor(this.remainSecond / (60 * 60)) - (day * 24);
        let minute = Math.floor(this.remainSecond / 60) - (day * 24 * 60) - (hour * 60);
        let second = Math.floor(this.remainSecond) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        if (this.remainSecond > 0) {
          let d = String(day);
          let m = String(minute);
          let s = String(second);
          let h = String(hour);
          this.day = d; // Math.floor(this.remainSecond / (60 * 60 * 24));
          this.hour = h; // Math.floor(this.remainSecond / (60 * 60)) - (day * 24);
          this.minute = m; //Math.floor(this.remainSecond / 60) - (day * 24 * 60) - (hour * 60);
          this.second = s; // Math.floor(this.remainSecond) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
        }
      }
    },
    changeImg: function() {
      this.imgChange = !this.imgChange;
    },
    jumpCharge: function() {
      if (this.isShare) {
        this.jumpDownload();
      }else{
        window.location.href = "http://t.xiehou360.com/6@#" + 1;
      }
    },
    jumpDownload: function() {
      httpUtils.jumpDownloadUrl();
    },
    reload: function() {
      window.location.reload();
    },
  }
}

</script>
<style>
.refurbish {
  width: 1rem;
  height: 1rem;
  background: url("http://lianai-image-sys.qiniudn.com/mq/refurbishBtn.png") no-repeat center center;
  -webkit-background-size: 100% 100%;
  background-size: 100% 100%;
  position: fixed;
  bottom: 30%;
  right: 0;
}

.r_footer_block {
  width: 100%;
  height: 2rem;
}

.r_footer {
  position: fixed;
  bottom: 0;
}

.down {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
}

.footer {
  margin-bottom: 1rem;
}
.paybtn{
    position: absolute;
    width:2.17rem;
    height:0.62rem;
    display: block;
    background: url("http://lianai-image-sys.qiniudn.com/t/pay.png") no-repeat center center;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    top:13.2rem;
    left:50%;
    margin-left:-1.085rem;
}

</style>



